import { Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { Slide } from "react-awesome-reveal";
import { MiddleBlockSection, Content, ContentWrapper } from "./styles";

const Work = ({ id }: { id: string }) => {
  let data =
    [
      {
        "company": "Appstage Pty Ltd.",
        "title": "Software Developer",
        "years": "March 2020 - Present",
        "description": "Fullstack development using .Net MVC in an Agile environment as well react native dev for mobile front-ends. Responsibilities included working with senior developers to fix bugs for legacy systems and implement features for new projects."
      },
      {
        "company": "Steelkon Projects",
        "title": "Junior Wordpress Developer",
        "years": "April 2019 - February 2020",
        "description": "Maintain company wordpress website, including managing content, fixing bugs and adding additional features."
      }
    ];

  let work = data.map(function (work) {
    return <div key={work.company}>
      <span style={{
        fontSize: "24px",
        fontWeight: 700
      }}>{work.company}</span>
      <p className="work-info">{work.title} <span>&bull;</span> <em className="work-date">{work.years}</em></p>
      <p>{work.description}</p>
    </div>
  });

  return (
    <MiddleBlockSection>
      <Slide direction="up">
        <Row justify="center" align="middle" id={id}>
          <ContentWrapper>
            <Col lg={24} md={24} sm={24} xs={24}>
              <h6>Work </h6>
              <Content>
                <div>
                  {work.map(w => w)}
                </div>
              </Content>
            </Col>
          </ContentWrapper>
        </Row>
      </Slide>
    </MiddleBlockSection >
  );
};

export default withTranslation()(Work);
